.App {
  text-align: center;
  
  
}

.row {
  display: flex;
  justify-content: center;
  
}

body {
  background: url("../img/bg-image.jpg");  
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
    
}

@media (max-width: 450px) {
  body {
    background: url("../img/bg-image-rotate.jpg");  
    background-size:180%;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    
  }
    

}

.footer {
  position: fixed;
  display: flex;
  flex-direction: row;
  bottom: 5px;
  margin-left: 45%;
  width: 30px;
  max-width: 30px;
}

@media (max-width: 450px) {
  .footer {
    position: fixed;
    display: flex;
    flex-direction: row;
    bottom: 5px;
    margin-left: 38%;
    width: 30px;
    max-width: 30px;
  }
    

}


.image {
  margin: 1px;
  width: 33px;
}

@media (max-width: 450px) {
  .image {
    margin: 1px;
    width: 33px;
  }
    

}

